import React from "react";
import { Redirect } from "react-router-dom";

// Authentication related pages
import Login from "../pages/Authentication/Login";
import Logout from "../pages/Authentication/Logout";


// Dashboard
import Dashboard from "../pages/Dashboard/index";

//Ecommerce Page

import Jobs from "../pages/Ecommerce/EcommerceOrders/index"
import Users from "../pages/Ecommerce/EcommerceCustomers/index"



// Users
import AddUser from "../pages/Forms/AddUser";
import Profile from "../pages/Forms/Profile";
import UpdateUser from "../pages/Forms/UpdateUser"


//Jobs
import AddJobs from "../pages/Jobs/AddJobs";
import EditJob from "../pages/Jobs/EditJob";


//Kanban Board

const authProtectedRoutes = [
	{ path: "/users", component: Users },
	{ path: "/update-user/:id", component: UpdateUser}, 
	{ path: "/profile", component: Profile },
	{ path: "/add-user", component: AddUser },
	{ path: "/jobs", component: Jobs },
	{ path: "/new-job", component: AddJobs },
	{ path: "/edit-job/:id", component: EditJob },
	

	// { path: "/ecommerce-shops", component: EcommerceProducts },


	{ path: "/dashboard", component: Dashboard },

	// this route should be at the end of all other routes
	{ path: "/", exact: true, component: () => <Redirect to="/dashboard" /> }
];

const publicRoutes = [
	{ path: "/logout", component: Logout },
	{ path: "/login", component: Login },


	// // Authentication Inner
	// { path: "/auth-login", component: Login1 },
	// { path: "/auth-register", component: Register1 },
	// { path: "/auth-recoverpw", component: ForgetPwd1 },

	// { path: "/maintenance", component: Maintenance },
	// { path: "/comingsoon", component: CommingSoon },
	// { path: "/404", component: Error404 },
	// { path: "/500", component: Error500 },
];

export { authProtectedRoutes, publicRoutes };
